<template>
  <div id="brokenLine" class="brokenLine"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {},

  methods: {
    loadChart(title,s) {
      this.myChart = echarts.init(document.getElementById("brokenLine"));
      this.myChart.setOption(this.initOption(title,s));
    },

    init(data) {
      var s = [];
      var f = [];
      var p = [];
      var title = [];
      for (var j in data) {
        title.push(j);
        s.push(data[j] ? data[j].livepercent*100 : "");
      }
      this.loadChart(title,s);
    },

    initOption(title,s) {
      return {
        title: {
          text: "生存率趋势图",
          left: "50",
          top: 0,
          textStyle: {
            color: "#00FFFF"
          }
        },
        tooltip: {
          trigger: "axis",
          formatter: function(seriesData, ticket, callback) {
            var showHtm = "";
            for (var i = 0; i < seriesData.length; i++) {
              var name = seriesData[i].seriesName;
              var text = seriesData[i].name;
              var value = seriesData[i].value;
              if (i == 0) {
                showHtm = text + "<br>";
              }
              showHtm += name + ": " + value + "%" + "<br>";
            }
            return showHtm;
          },
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        legend: {
          textStyle: {
            color: "#05fad5",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          },
          data: ["生存率"]
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
            data: title
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "%",
            min: 0,
            max: 100,
            interval: 20,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
            axisLabel: {
              formatter: "{value}"
            }
          },
          {
            type: "value",
            name: "%",
            min: 0,
            max: 100,
            interval: 20,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
            axisLabel: {
              formatter: "{value}"
            }
          }
        ],
        color: ["#1ed674", "#ff6567", "#1c84ff", "#f9ff00", "#ffa9a8"],
        series: [
          {
            name: "生存率",
            itemStyle: {
              barBorderRadius: 13
            },
            barWidth : 20,
            type: "bar",
            data: s
          },
          {
            name: "生存率",
            type: "line",
            yAxisIndex: 1,
            data: s
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
.brokenLine {
  width: 100%;
  height: 400px;
  float: left;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 10px;
  padding-top: 40px;
  overflow: hidden;
}
</style>
