<template>
  <div id="brokenLine7" class="brokenLine7"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {},

  methods: {
    loadChart(title,P255,P10,P12,P15) {
      this.myChart = echarts.init(document.getElementById("brokenLine7"));
      this.myChart.setOption(this.initOption(title,P255,P10,P12,P15));
    },

    init(data) {
      console.log("111111规避次数统计趋势图",data);
      var p255 = [];
      var p10 = [];
      var p12 = [];
      var p15 = [];
      var title = [];
      for (var j in data) {
        title.push(j);
        if (data[j]) {
          p255.push(data[j].hide[255] ? data[j].hide[255] : 0);
          p10.push(data[j].hide[10] ? data[j].hide[10] : 0);
          p12.push(data[j].hide[12] ? data[j].hide[12] : 0);
          p15.push(data[j].hide[15] ? data[j].hide[15] : 0);
        }
      }
      console.log("111111规避次数统计趋势图",title,p255,p10,p12,p15);
      this.loadChart(title,p255,p10,p12,p15);
    },

    initOption(title,P255,P10,P12,P15) {
      return {
        title: {
          text: "规避次数统计趋势图",
          left: "50",
          top: 0,
          textStyle: {
            color: "#00FFFF"
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        legend: {
          textStyle: {
            color: "#05fad5",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          },
          data: ["航炮","P-10","P-12","P-15"]
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
            data: title
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "次数",
            min: 0,
            max: 10,
            interval: 2,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
            axisLabel: {
              formatter: "{value} 次"
            }
          },
          {
            type: "value",
            name: "场次",
            min: 0,
            max: 10,
            interval: 2,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
            axisLabel: {
              formatter: "{value} 次"
            }
          }
        ],
        color: ["#1ed674", "#ff6567", "#1c84ff", "#f9ff00", "#ffa9a8"],
        series: [
          {
            name: "航炮",
            type: "line",
            yAxisIndex: 1,
            data: P255
          },
          {
            name: "P-10",
            type: "line",
            yAxisIndex: 1,
            data: P10
          },
          {
            name: "P-12",
            type: "line",
            yAxisIndex: 1,
            data: P12
          },
          {
            name: "P-15",
            type: "line",
            yAxisIndex: 1,
            data: P15
          },
        ]
      };
    }
  }
};
</script>
<style lang="less">
.brokenLine7 {
  width: 100%;
  height: 400px;
  float: left;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 10px;
  padding-top: 40px;
  overflow: hidden;
}
</style>
