import request from '@/utils/axios'




//f攻击能力趋势查询
export function trend(params) {
  return request({
    url: '/power/f/trend',
    method: 'post',
    params
  })
}

//f攻击能力分析
export function fAnalysis(params) {
  return request({
    url: '/power/f',
    method: 'post',
    params
  })
}






